<template>
  <div>
    <el-form ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item :label="$t('TabLable_addLabel')"   prop="isSynchronize">
        <el-button icon="$t('TabLable_Label')"  class="submit" size="mini" style="margin-left: 0" @click="addPort">添加标签</el-button>
<!--        标签-->
      </el-form-item>
      <el-form-item :label="$t('TabLable_Label')" prop="port" v-for="(item, index) in portNum" :key="item">
        <el-input :placeholder="$t('TabLable_Label')" v-model="Lables[index].key" style="width: 40%;">
          <template slot="$t('TabLable_Label')">name</template>
<!--          请输入key值-->
        </el-input>
        <el-input :placeholder="$t('TabLable_LabelParameter')" v-model="Lables[index].value" style="width: 40%;margin-left: 20px;">
          <template slot="$t('TabLable_LabelParameter')">value</template>
<!--          标签参数-->
        </el-input>
        <el-button @click="delPort(item, index)" title="删除" type="danger" icon="iconfont icon-lajitong" style="margin-left: 10px; padding: 10px;" size="mini"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      Lables: [],
      portNum: [],
      i: 0
    }
  },
  watch: {
    // 监听vuex中是isupload是否变化为true，true则上传本组件的数据。
    '$store.state.isUpload': function () {
      if (this.$store.state.isUpload) {
        this.$store.commit('changeCrateData', ['Lables', this.Lables])
      }
    }
  },
  methods: {
    // 添加开放端口的input框
    addPort () {
      this.portNum.push(this.i)
      this.Lables.push({ key: '', value: '' })
      this.i++
    },
    // 删除开放端口的input框
    delPort (item, index) {
      this.portNum = this.portNum.filter(data => {
        return data !== item
      })
      this.Lables.splice(index, 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.submit{
  background: #067FB5;
  color: #fff;
  margin-left: 30px;
}
</style>
